@import "definitions";

.logo {
  white-space: pre;
  background: $gradient;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  @include animated-background;

  display: flex;
  justify-content: center;

  font-family: $monospace-font;
  font-size: $font-size-base;
  line-height: 1em;
  height: 336px;
  @include breakpoint-down($breakpoint-xl) {
    font-size: 12px;
    height: 247px; }
  @include breakpoint-down($breakpoint-lg) {
    font-size: 10px;
    height: 208px; }
  @include breakpoint-down($breakpoint-md) {
    font-size: 8px;
    height: 167px; }
  @include breakpoint-down($breakpoint-sm) {
    font-size: 6px;
    height: 125px; }
  @include breakpoint-down($breakpoint-xs) {
    font-size: 5px;
    height: 104px; } }

.logo--kling {
  align-self: center; }

.logo--klang {
  align-self: flex-end; }

.logo--flut {
  align-self: flex-start; }
